import { PlatformizedFilterBuilder } from '../../filter'
import { ReferencedItemOptions } from '../../types'
import { Sort } from '../../sort/sortMixin'
import { WixDataResult } from '../WixDataResult'
import { WixDataQueryOptions, WixDataReadOptions } from '../types'
import { QueryValidator } from '../QueryValidator'
import { QueryBase } from '../QueryBase'
import { WixDataQuery } from '../WixDataQuery'

type OnFind = (
  args: IArguments,
  params: QueryParams,
  options?: WixDataQueryOptions
) => Promise<WixDataResult>

type OnDistinct = (
  args: IArguments,
  params: QueryParams,
  field: string,
  options?: WixDataQueryOptions
) => Promise<WixDataResult<any>>

type OnCount = (
  args: IArguments,
  params: QueryParams,
  options?: WixDataReadOptions
) => Promise<number>

export type QueryParams = {
  collectionName: string
  invalidArguments: string[]
  filterTree?: Record<string, any>
  projectedFields: string[]
  limitNumber?: number
  skipNumber: number
  included: ReferencedItemOptions[]
  orderBy: { [field: string]: 'asc' | 'desc' }[]
  randomSortOptions?: { seed?: number }
}

export class WixDataQueryImpl
  extends QueryBase<WixDataQueryImpl>
  implements WixDataQuery
{
  readonly projectedFields: string[]

  readonly limitNumber?: number

  readonly skipNumber: number

  readonly included: ReferencedItemOptions[]

  readonly randomSortOptions: { seed?: number } | undefined

  private readonly onFind: OnFind
  private readonly onDistinct: OnDistinct
  private readonly onCount: OnCount

  private readonly ownInvalidArguments: string[]

  constructor(origin: {
    collectionName: string
    filterBuilder?: PlatformizedFilterBuilder
    invalidArguments?: string[]
    projectedFields?: string[]
    limitNumber?: number
    skipNumber?: number
    included?: ReferencedItemOptions[]
    randomSortOptions?: { seed?: number }
    sort?: Sort
    onFind: OnFind
    onDistinct: OnDistinct
    onCount: OnCount
  }) {
    super(origin)
    this.projectedFields = origin?.projectedFields ?? []
    this.limitNumber = origin?.limitNumber
    this.skipNumber = origin?.skipNumber ?? 0
    this.included = origin?.included ?? []
    this.onFind = origin.onFind
    this.onDistinct = origin.onDistinct
    this.onCount = origin.onCount
    this.ownInvalidArguments = origin.invalidArguments ?? []
    this.randomSortOptions = origin.randomSortOptions
  }

  protected override copy(params: {
    filterBuilder?: PlatformizedFilterBuilder
    projectedFields?: string[]
    limitNumber?: number
    skipNumber?: number
    included?: ReferencedItemOptions[]
    sort?: Sort
    invalidArguments?: string[]
    randomSortOptions?: { seed?: number }
  }): WixDataQueryImpl {
    return new WixDataQueryImpl({
      ...this,
      filterBuilder: params.filterBuilder ?? this.filterBuilder,
      projectedFields: params.projectedFields ?? this.projectedFields,
      limitNumber: params.limitNumber ?? this.limitNumber,
      skipNumber: params.skipNumber ?? this.skipNumber,
      included: params.included ?? this.included,
      sort: params.sort ?? this.sort,
      invalidArguments: params.invalidArguments ?? this.ownInvalidArguments,
      randomSortOptions: params.randomSortOptions ?? this.randomSortOptions,
      onFind: this.onFind,
      onDistinct: this.onDistinct,
      onCount: this.onCount,
    })
  }

  count(options?: WixDataReadOptions): Promise<number> {
    return this.onCount(arguments, this.toQueryParams(), options)
  }

  distinct(
    field: string,
    options?: WixDataQueryOptions
  ): Promise<WixDataResult<any>> {
    return this.onDistinct(arguments, this.toQueryParams(), field, options)
  }

  find(options?: WixDataQueryOptions): Promise<WixDataResult> {
    return this.onFind(arguments, this.toQueryParams(), options)
  }

  get invalidArguments(): string[] {
    return [
      ...this.ownInvalidArguments,
      ...this.filterBuilder.invalidArguments,
      ...this.sort.invalidArguments,
    ]
  }

  private toQueryParams(): QueryParams {
    let filterTree
    try {
      // optimized filter if no errors
      filterTree = this.filterBuilder.build()
    } catch (_error: unknown) {
      filterTree = this.filterBuilder.filterTree
    }
    return {
      collectionName: this.collectionName,
      invalidArguments: this.invalidArguments,
      projectedFields: this.projectedFields,
      limitNumber: this.limitNumber,
      skipNumber: this.skipNumber,
      included: this.included,
      filterTree,
      orderBy: this.orderBy,
      randomSortOptions: this.randomSortOptions,
    }
  }

  fields(...fields: string[]): WixDataQuery {
    return this.copy({
      projectedFields: [...this.projectedFields, ...fields],
    })
  }

  limit(limitNumber: number) {
    const [invalidArguments] = this.queryValidator('.limit')
      .arityIsOne(arguments)
      .isPositiveNumber(limitNumber)
      .isInteger(limitNumber)
      .validateAndAggregate()
    return this.copy({
      invalidArguments,
      limitNumber,
    })
  }

  skip(skipNumber: number) {
    const [invalidArguments] = this.queryValidator('.skip')
      .arityIsOne(arguments)
      .isNonNegativeNumber(skipNumber)
      .isInteger(skipNumber)
      .validateAndAggregate()
    return this.copy({
      invalidArguments,
      skipNumber,
    })
  }

  include(...fieldNames: string[]): WixDataQuery

  include(fieldName: string, limit?: number): WixDataQuery

  include(fieldName1: string, fieldName2: string, limit?: number): WixDataQuery

  include(
    fieldName1: string,
    fieldName2: string,
    fieldName3: string,
    limit?: number
  ): WixDataQuery

  include(
    fieldName1: string,
    fieldName2: string,
    fieldName3: string,
    fieldName4: string,
    limit?: number
  ): WixDataQuery

  include(
    fieldName1: string,
    fieldName2: string,
    fieldName3: string,
    fieldName4: string,
    fieldName5: string,
    limit?: number
  ): WixDataQuery

  include(...fieldNamesAndLimit: [...string[], number]): WixDataQuery

  include(...args: unknown[]): WixDataQuery {
    if (args.length === 0) {
      return this
    }

    const last = args[args.length - 1]
    const limit: number | undefined =
      typeof last === 'number' ? last : undefined

    const expectedFieldNameCount =
      limit === undefined ? args.length : args.length - 1

    const newIncludes = args
      .slice(0, expectedFieldNameCount)
      .map((fieldName) => ({
        fieldName: fieldName as string,
        limit,
      }))

    const [invalidArguments] = args
      .slice(0, expectedFieldNameCount)
      .reduce<QueryValidator>(
        (validator: QueryValidator, value: unknown, _, __) =>
          validator.nonEmptyString(value),
        this.queryValidator('.include')
      )
      .validateAndAggregate()

    return this.copy({
      invalidArguments,
      included: [...this.included, ...newIncludes],
    })
  }

  randomSort({ seed }: { seed?: number }): WixDataQuery {
    const [invalidArgs1] = this.queryValidator('.randomSort')
      .arityIsOne(arguments)
      .validateAndAggregate()

    const [invalidArgs2] = this.queryValidator('.randomSort.seed')
      .isOptionalInteger(seed)
      .validateAndAggregate()

    const invalidArguments = [...invalidArgs1, ...invalidArgs2]

    return this.copy({
      invalidArguments,
      randomSortOptions: { seed },
    })
  }

  private queryValidator(
    operatorName: string,
    invalidArguments = this.ownInvalidArguments
  ) {
    return new QueryValidator(operatorName, invalidArguments)
  }
}
